<template>
    <div>
      <h1>Feel free to choose how to Sign in!</h1>
      <el-button round><router-link to="/signin">Sign in with Email</router-link></el-button><br><br>
      <el-button round @click="userSignUpWithGoogle">Sign in with Google</el-button>
    </div>
</template>

<script>
  export default {
    name: "Login",
    methods: {
      async userSignUpWithGoogle () {
        await this.$store.dispatch('signInWithGoogle')
      }
    }
  }
</script>

<style scoped>

</style>



